import { FC, useRef, useState, useEffect, useContext } from "react";
import classNames from "classnames/bind";

import { Icon } from "common/components/Icons";
import LogoIcon from "common/components/Icons/icons/logo.svg";
import CopyIcon from "common/components/Icons/icons/24/copy.svg";
import DoneIcon from "common/components/Icons/icons/24/done.svg";
import { Picture } from "common/components/Picture";
import { Text } from "common/components/Text";
import { Toast } from "common/components/Toast";
import { Heading } from "common/components/new/Heading";
import { Button, ButtonSize } from "common/components/new/Button";
import { Link } from "common/components/Link";
import { LoadingIcon } from "common/components/Icons/LoadingIcon";
import { ColorsStroke } from "common/components/Icons/LoadingIcon/types";
import {
    getAnalyticsId,
    GetAnalyticsIdElement,
} from "common/utils/AnalyticsId";
import { ErrorType } from "common/components/ErrorLayout";
import { ErrorContext } from "common/context/errorContext";
import { getRefCodeFromQuery } from "common/utils/referral";

import { cities } from "navigation/cities";

import { ReferralForFriendsPageProps } from "./ReferralForFriendsPageLayout.types";
import styles from "./ReferralForFriendsPageLayout.module.scss";

const cx = classNames.bind(styles);

export const ReferralForFriendsPageLayout: FC<ReferralForFriendsPageProps> = ({
    content,
}) => {
    const {
        title,
        subtitle,
        description,
        image,
        buttonText,
        toastText,
        terms,
        contacts,
    } = content;

    const inputRef = useRef<HTMLInputElement | null>(null);
    const { setErrorType } = useContext(ErrorContext);

    const [showToast, setShowToast] = useState(false);
    const [shortLink, setShortLink] = useState("");

    const copyText = () => {
        inputRef?.current?.select();
        document.execCommand("copy");
        setShowToast(true);

        const timeout = setTimeout(() => setShowToast(false), 1600);

        return () => clearTimeout(timeout);
    };

    // const getLink = async () => {
    //     if (code) {
    //         const response = await getReferralLink(code as string);
    //         if (response) {
    //             const {
    //                 data: { data },
    //             } = response;
    //             setShortLink(data?.shortLink);
    //         } else {
    //             setHasApiError(true);
    //         }
    //     } else {
    //         setHasApiError(true);
    //     }
    // };

    useEffect(() => {
        let link = window?.location?.href;
        const code = getRefCodeFromQuery();

        if (code) {
            link = link.replace(cities.referralForFriends, cities.referral);
        } else {
            setErrorType(ErrorType.ADVOCATE);
        }
        setShortLink(link);
    }, []);

    return (
        <div className={cx("root")}>
            <Picture
                className={cx("picture")}
                classNameImg={cx("picture__image")}
                mobile={image.mobile}
                desktop={{ ...image.desktop, media: "(min-width: 1024px)" }}
            />
            <div className={cx("content")}>
                <Link
                    className={cx("logo")}
                    {...contacts.site}
                    id={getAnalyticsId({
                        zone: "header",
                        element: GetAnalyticsIdElement.LINK,
                        label: "logo",
                    })}
                >
                    <Icon className={cx("logo__icon")}>
                        <LogoIcon />
                    </Icon>
                </Link>
                <Heading className={cx("title")}>
                    <span dangerouslySetInnerHTML={{ __html: title }} />
                </Heading>
                <Text className={cx("subtitle")}>{subtitle}</Text>
                <section className={cx("link")}>
                    {shortLink ? (
                        <>
                            <input
                                className={cx("input")}
                                value={shortLink}
                                ref={inputRef}
                                readOnly
                            />
                            <Button
                                className={cx("button")}
                                size={ButtonSize.M}
                                icon={<CopyIcon />}
                                onClick={copyText}
                            >
                                {buttonText}
                            </Button>
                            <Toast
                                className={cx("toast", {
                                    toast_visible: showToast,
                                })}
                            >
                                <Icon className={cx("toast__icon")}>
                                    <DoneIcon />
                                </Icon>
                                {toastText}
                            </Toast>
                        </>
                    ) : (
                        <LoadingIcon
                            className={cx("loading-icon")}
                            stroke={ColorsStroke.DARK_GRAY}
                        />
                    )}
                </section>
                <Text className={cx("description")}>
                    <span dangerouslySetInnerHTML={{ __html: description }} />
                </Text>
                <footer className={cx("footer")}>
                    <Link link={terms.link} target="_blank">
                        <Text className={cx("terms")}>
                            <span
                                dangerouslySetInnerHTML={{
                                    __html: terms.title,
                                }}
                            />
                        </Text>
                    </Link>
                </footer>
            </div>
        </div>
    );
};
